import React from "react"

import Layout from "../components/Layout/Layout"
import SEO from "../components/Header/SEO/SEO"
import Entry from "../components/Content/Entry/Entry"

const ImpressumPage = () => (
  <Layout>
    <SEO title="Blog" lang="de" description="Der Blog von Karl-Katja Krach widmet sich kritisch Philosophie, Politik, Medien und Religion. Außerdem wird hier veröffentlicht, was Karl-Katja Krach sonst so produziert: Musik, Hörspiele, Videos, Texte und was auch immer." keywords={['Blog', 'Krach', 'Karl-Katja Krach', 'Kritik', 'Philosophie', 'Politik', 'Gesellschaft', 'Medien', 'Kultur', 'Musik', 'Impressum']} />
    <Entry title='Impressum'>
      <p>Verantwortlich für diese Seite ist Karl-Katja Krach.</p>
      <p>Kontakt: karlkatjakrach [at] mailbox [punkt] org</p>
    </Entry>
  </Layout>
)

export default ImpressumPage